<template>
  <div>
    <el-row>
      <el-col
        :span="4"
        v-for="(item,index) in this.$store.getters.getGroupList"
        :key="item.groupId"
        :offset="index % 5 > 0 ? 1 : 0"
      >
        <el-card shadow="hover" :body-style="{ padding: '0px' }">
          <img
            :src="item.groupIcon"
            class="image"
            @click="$router.push({path:'activity',query:{id:item.groupId}})"
            @mousedown.prevent
            title="点击查看详细信息"
          />
          <div style="padding: 8px;">
            <span>{{item.groupName}}</span>
            <div class="bottom clearfix">
              <time class="time">{{ currentDate }}</time>
              <el-button type="text" class="button" @click="dialogVisible = true">详细信息</el-button>
              <el-button type="text" class="button">查看活动</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <div style="position:absolute;bottom:20px;left:45%">
      <el-pagination
        :hide-on-single-page="true"
        background
        layout="prev, pager, next"
        :current-page="Number($store.state.actGroup.pageNum)"
        :total="this.$store.state.actGroup.data.total"
        @current-change="getGroupList"
      ></el-pagination>
    </div>

    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <span>这是一段信息</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<style>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>

<script>
export default {
  data() {
    return {
      currentDate: new Date().toString().substr(0, 10),
      dialogVisible: false
    };
  },
  created() {
    this.getGroupList();
  },
  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {
          this.$loading()
        });
    },
    getGroupList(pageNum = this.$store.state.actGroup.pageNum) {
      this.$api.getAllGroup({ pageNum: pageNum, pageSize: 10 }).then(data => {
        //console.log(data);
        this.$store.dispatch("pushGroup", {
          name: "group",
          num: pageNum,
          data: data.data
        });
      });
    }
  }
};
</script>


